import React, { Children } from 'react'
import styled from 'styled-components'
import PropTypes from 'prop-types'
import { GridContainer, GridRow, GridColumn, Steps, Step, StepContent, StepPanel, StepProvider } from '@resultadosdigitais/tangram-components'

const id = title =>
  title
    .toLowerCase()
    .normalize('NFD')
    .replace(/ /g, '-')
    .replace(/[\u0300-\u036f]/g, '')

const Wizard = styled(({ className, steps, activeStep, children, ...rest }) => (
  <StepProvider {...rest} className={className} activeStep={activeStep}>
    <GridContainer>
      <GridRow>
        <GridColumn mobile={3} tablet={3} desktop={3}>
          <Steps>
            {steps.map((title, index) => (
              <Step id={id(title)} key={title} completed={index < activeStep}>
                {title}
              </Step>
            ))}
          </Steps>
        </GridColumn>
        <GridColumn mobile={9} tablet={9} desktop={9}>
          <StepContent>
            {Children.map(children, (child, i) => (
              <StepPanel aria-labelledby={id(steps[i])}>{child}</StepPanel>
            ))}
          </StepContent>
        </GridColumn>
      </GridRow>
    </GridContainer>
  </StepProvider>
))`
  [class^='Steps__Root'] {
    width: 100%;
  }

  [class^='StepContent__Root'] {
    width: 100%;
  }
`

Wizard.defaultProps = {
  activeStep: 0,
}

Wizard.propTypes = {
  /**
   * sets a step as active through its index
   */
  activeStep: PropTypes.number,
}

export default Wizard
