import React from 'react'
import styled from 'styled-components'
import { useTranslation } from 'react-i18next'
import { Text, Button, Drawer } from '@resultadosdigitais/tangram-components'
import { Question, Times } from '@resultadosdigitais/tangram-react-icons'
import { useLayout } from '../index'

const Toggler = styled(({ className }) => {
  const { t } = useTranslation()
  const { isSidebarOpened, toggleSidebar } = useLayout()

  return (
    <div className={className}>
      {!isSidebarOpened && (
        <Text as="label" htmlFor="toggler" token={Text.tokens.TEXT_SM_REGULAR}>
          {t('doubts')}
        </Text>
      )}
      <Button id="toggler" onClick={toggleSidebar}>
        {isSidebarOpened ? <Times /> : <Question />}
      </Button>
    </div>
  )
})`
  position: fixed;
  top: var(--size-spacing-05);
  right: 0;
  display: flex;
  align-items: center;

  label {
    cursor: pointer;
    padding: var(--size-spacing-02) var(--size-spacing-04);
    background: var(--color-purple-20);
    border-radius: var(--border-radius-xs) 0px 0px var(--border-radius-xs);
  }

  button {
    background: var(--color-purple-50);
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;

    :hover {
      background: var(--color-purple-40);
    }
  }
`

const Action = styled(Button)`
  background: var(--color-gray-50);

  :hover {
    background: var(--color-gray-40);
  }
`

const Sidebar = () => {
  const { t } = useTranslation()
  const { isSidebarOpened, toggleSidebar } = useLayout()

  return (
    <>
      <Toggler />
      <Drawer anchor={Drawer.anchors.right} size={Drawer.sizes.md} scrim={true} open={isSidebarOpened}>
        <Drawer.Header onDismiss={toggleSidebar}>
          <Drawer.Title>{t('how_to_import')}</Drawer.Title>
        </Drawer.Header>
        <Drawer.Body>...</Drawer.Body>
        <Drawer.Actions>
          <Action>{t('access_completed_article')}</Action>
        </Drawer.Actions>
      </Drawer>
    </>
  )
}

export default Sidebar
