import styled from 'styled-components'
import { useHistory, useParams } from 'react-router-dom'
import { Text } from '@resultadosdigitais/tangram-components'
import { pathnames, useQuery } from '../../../routes'
import { Footer } from '../../../layout'

const Title = styled(Text).attrs(() => ({
  forwardedAs: 'h3',
  token: Text.tokens.TEXT_LG_BOLD,
}))`
  margin-top: 0;
  margin-bottom: var(--size-spacing-05);
`

const Subtitle = styled(Text).attrs(() => ({
  forwardedAs: 'h5',
  token: Text.tokens.TEXT_MD_BOLD,
}))`
  display: flex;
  width: 100%;
  margin-top: 0;
  margin-bottom: ${({ marginBottom }) => (marginBottom ? `var(--${marginBottom})` : '0')};
`

const Description = styled(Text).attrs(() => ({
  token: Text.tokens.TEXT_SM_REGULAR,
}))`
  width: 100%;
  margin-top: 0;
  margin-bottom: ${({ marginBottom }) => (marginBottom ? `var(--${marginBottom})` : '0')};
`

const Panel = styled.div`
  padding-bottom: calc(var(--size-spacing-10) + 88px); /* 88 is footer height */
`

Panel.Footer = Footer

const slugFrom = o => Object.values(o).join('/')
const mergeQueries = (t, s) => Object.entries(s || {}).forEach(entry => t.set(...entry))

const usePanelNavigation = () => {
  const history = useHistory()
  const params = useParams()
  const query = useQuery()
  const openPanel = (options = {}) => {
    const slug = slugFrom({ ...params, ...options.slug })
    const pathname = slug ? `${pathnames.GENERAL_DATA}/${slug}` : pathnames.GENERAL_DATA
    mergeQueries(query, options.query)
    history.push(`${pathname}?${query.toString()}`)
  }
  return { params, query, openPanel }
}

export { Title, Subtitle, Description, Panel, usePanelNavigation }
