import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { useTranslation } from 'react-i18next'
import { GridRow, GridColumn, Table, Button } from '@resultadosdigitais/tangram-components'
import { Ban, Building, Person, Case, ArrowRight } from '@resultadosdigitais/tangram-react-icons'
import fieldsResource from '../../../resources/fields'
import reviewService from '../../../services/review'
import SpreadsheetColumnExcerpt from '../components/SpreadsheetColumnExcerpt'
import { Panel, usePanelNavigation, Title, Subtitle } from './Panels'

const DataGroupContainer = styled.div`
  margin-bottom: var(--size-spacing-06);
`

const DataGroupName = styled.div`
  margin-left: var(--size-spacing-03);
`

const FieldNameContainer = styled.div`
  width: 400px;
  display: flex;
  align-items: center;
  font: var(--text-md-bold);
  svg {
    margin-right: var(--size-spacing-05);
    fill: var(--color-green-40);
  }
`

const getGroupIcon = groupName => {
  const availableIcons = {
    'Empresa/Cliente': Building,
    Contato: Person,
    Oportunidade: Case,
  }
  return availableIcons[groupName] || Ban
}

const ReviewDataGroups = ({ spreadsheetHasHeader, groups }) => {
  const { t } = useTranslation()

  return groups.map((group, key) => {
    const DataGroupIcon = getGroupIcon(group.name)

    return (
      <DataGroupContainer key={key}>
        <GridRow>
          <GridColumn mobile={12} tablet={12} desktop={12}>
            <Subtitle marginBottom="size-spacing-05">
              <DataGroupIcon />
              <DataGroupName>{group.name || t('review_data_panel_ignored_columns')}</DataGroupName>
            </Subtitle>
          </GridColumn>
        </GridRow>
        <GridRow>
          <GridColumn mobile={12} tablet={12} desktop={12}>
            <Table>
              <Table.Head>
                <Table.Row>
                  <Table.Cell as="th">{t('review_data_panel_sheet_field')}</Table.Cell>
                  <Table.Cell as="th">{group.name ? t('review_data_panel_crm_field') : ''}</Table.Cell>
                </Table.Row>
              </Table.Head>
              <Table.Body>
                {group.columns.map((column, key) => (
                  <Table.Row key={key}>
                    <Table.Cell>
                      <SpreadsheetColumnExcerpt hasHeader={spreadsheetHasHeader} isIgnored={!column.fieldName} columnItems={column.items} />
                    </Table.Cell>
                    <Table.Cell>
                      {column.fieldName && (
                        <FieldNameContainer>
                          <ArrowRight /> {column.fieldName}
                        </FieldNameContainer>
                      )}
                    </Table.Cell>
                  </Table.Row>
                ))}
              </Table.Body>
            </Table>
          </GridColumn>
        </GridRow>
      </DataGroupContainer>
    )
  })
}

const WizardReviewDataPanel = ({ data, onDataSave }) => {
  const { t } = useTranslation()
  const { openPanel } = usePanelNavigation()
  const [fields, setFields] = useState()
  const handlePanelOpen = step => () => openPanel({ query: { step } })
  const handleImport = () => {
    const newData = { ...data, import_status: 'queued' }
    onDataSave({ newData })
  }

  useEffect(() => {
    fieldsResource.get().then(response => setFields(response.data.fields))
  }, [])

  return (
    <Panel>
      <GridRow>
        <GridColumn mobile={12} tablet={12} desktop={12}>
          <Title>{t('review_data_panel_title')}</Title>
        </GridColumn>
      </GridRow>
      {data.col_map && fields && (
        <ReviewDataGroups groups={reviewService.buildDataGoups(data.rows, data.col_map, fields)} spreadsheetHasHeader={data.has_header} />
      )}
      <Panel.Footer>
        <Button kind={Button.kinds.secondary} onClick={onDataSave}>
          {t('save_exit')}
        </Button>
        <Button kind={Button.kinds.secondary} onClick={handlePanelOpen('4')}>
          {t('back')}
        </Button>
        <Button kind={Button.kinds.primary} onClick={handleImport}>
          {t('import')}
        </Button>
      </Panel.Footer>
    </Panel>
  )
}

export default WizardReviewDataPanel
