import React, { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { usePanelNavigation } from './panels/Panels'
import resource from '../../resources/imports'
import { useQuery } from '../../routes'
import { Wizard } from '../../components'
import {
  WizardSheetContentPanel,
  WizardFileSelectionPanel,
  WizardPrivacySettingsPanel,
  WizardFieldsCombinationPanel,
  WizardPreferencesPanel,
  WizardReviewDataPanel,
} from './panels'

const labels = [
  'steps_choose_content',
  'steps_select_file',
  'steps_define_legal_base',
  'steps_combine_fields',
  'steps_define_preferences',
  'steps_review_data',
]

const GeneralData = () => {
  const { params, openPanel } = usePanelNavigation()
  const { t } = useTranslation()
  const [id] = useState(params.id)
  const [data, setData] = useState({})
  const steps = labels.map(label => t(label))
  const activeStep = parseInt(useQuery().get('step') || '0')
  const handleDataChange = (attrs, { shouldPersist } = {}) => {
    const newData = { ...data, ...attrs }
    setData(newData)
    if (shouldPersist) saveData(newData)
  }
  const handleDataSave = ({ newData = data, step }) => {
    saveData(newData).then(() => {
      if (step) openPanel({ query: { step } })
    })
  }
  const saveData = newData => {
    return resource.update(newData.id, newData).then(response => setData(response.data))
  }

  useEffect(() => {
    if (id) resource.get(id).then(response => setData(response.data))
  }, [id])

  return (
    <Wizard steps={steps} activeStep={activeStep}>
      <WizardSheetContentPanel data={data} onDataChange={handleDataChange} />
      <WizardFileSelectionPanel data={data} onDataChange={handleDataChange} />
      <WizardPrivacySettingsPanel data={data} onDataChange={handleDataChange} onDataSave={handleDataSave} />
      <WizardFieldsCombinationPanel data={data} onDataChange={handleDataChange} onDataSave={handleDataSave} />
      <WizardPreferencesPanel data={data} onDataChange={handleDataChange} onDataSave={handleDataSave} />
      <WizardReviewDataPanel data={data} onDataSave={handleDataSave} />
    </Wizard>
  )
}

export { GeneralData as default }
