import React, { useState } from 'react'
import styled from 'styled-components'
import { useTranslation } from 'react-i18next'
import { GridRow, GridColumn, DropFile as DropFileStyle, Button } from '@resultadosdigitais/tangram-components'
import resource from '../../../resources/imports'
import { Panel, usePanelNavigation, Title } from './Panels'

const DropFile = styled(DropFileStyle)`
  width: 100%;
`

const WizardFileSelection = ({ onDataChange }) => {
  const { t } = useTranslation()
  const { query, openPanel } = usePanelNavigation()
  const type = query.get('type')
  const [id, setId] = useState()
  const onUpload = file => resource.save({ file, type })
  const onUploadSuccess = ({ data }) => {
    onDataChange(data)
    setId(data.id)
  }
  const handlePanelOpen = step => () => openPanel({ slug: { id }, query: { step, type } })

  return (
    <Panel>
      <GridRow>
        <GridColumn mobile={12} tablet={12} desktop={12}>
          <Title>{t('file_selection_panel_title')}</Title>
        </GridColumn>
      </GridRow>
      <GridRow>
        <GridColumn mobile={12} tablet={12} desktop={12}>
          <DropFile accept=".xls,.csv,.xlsx" onUpload={onUpload} onUploadSuccess={onUploadSuccess} autoUpload />
        </GridColumn>
      </GridRow>
      <Panel.Footer>
        <Button kind={Button.kinds.secondary} onClick={handlePanelOpen('0')}>
          {t('back')}
        </Button>
        <Button kind={Button.kinds.primary} onClick={handlePanelOpen('2')} disabled={!id}>
          {t('next')}
        </Button>
      </Panel.Footer>
    </Panel>
  )
}

export default WizardFileSelection
