import React from 'react'
import styled from 'styled-components'
import { Text } from '@resultadosdigitais/tangram-components'

const Container = styled.div`
  width: 315px;
  p {
    margin: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    + p {
      color: var(--color-gray-70);
    }
  }
  &.is-ignored {
    p {
      color: var(--color-gray-50);
      + p {
        color: var(--color-gray-50);
      }
    }
  }
`

const SpreadsheetColumnExcerpt = ({ hasHeader, columnItems, isIgnored }) => {
  const header = hasHeader ? columnItems.shift() : null
  return (
    <Container className={buildCssClasses(isIgnored)}>
      {header && <Text token={Text.tokens.TEXT_MD_BOLD}>{header}</Text>}
      <Text token={Text.tokens.TEXT_SM}>{removeUnnecessaryCommas(columnItems.join(', '))}</Text>
    </Container>
  )
}

function buildCssClasses(isIgnored) {
  return isIgnored ? 'is-ignored' : ''
}

function removeUnnecessaryCommas(text) {
  return text.trim().replace(/, ,/g, ',').replace(/,$/, '')
}

export default SpreadsheetColumnExcerpt
