import React, { createContext, useContext, useState, useEffect } from 'react'
import { withContext } from '@resultadosdigitais/front-hub/react'
import { fronthubInteropPortal as portal } from '@resultadosdigitais/crm-core'

const FronthubContext = createContext()

const FronthubProvider = withContext(({ children, ...initialContext }) => {
  const { Provider } = FronthubContext
  const [context, update] = useState(initialContext)
  const value = { ...context, update }

  useEffect(() => {
    portal.set('context', value)
  })

  return <Provider value={value}>{children}</Provider>
})

FronthubProvider.displayName = 'FronthubProvider'

const useFronthubContext = () => useContext(FronthubContext)

export { FronthubProvider as default, useFronthubContext }
