const _public = {}

_public.getGroupNameByFieldKey = (fields, fieldKey) => {
  const group = fields.find(field => {
    return field.options.find(option => option.key === fieldKey)
  })
  return group && group.label
}

_public.getFieldNameByFieldKey = (fields, fieldKey) => {
  let fieldName = ''
  fields.forEach(field => {
    const option = field.options.find(option => option.key === fieldKey)
    if (option) fieldName = option.label
  })
  return fieldName
}

export default _public
