import _extends from "@babel/runtime/helpers/esm/extends";
import { css } from 'styled-components';
import { breakpointSm, breakpointMd, breakpointLg, breakpointXl, sizeSpacing04, sizeSpacing05, sizeSpacing06, sizeSpacing08 } from '@resultadosdigitais/tangram-design-tokens';
export const DIMENSIONS = ['mobileSmall', 'mobile', 'tablet', 'desktop', 'desktopLarge'];
export const BASE_CONF = {
  columns: {
    mobileSmall: 4,
    mobile: 8,
    tablet: 12,
    desktop: 12,
    desktopLarge: 12
  },
  gutterWidth: {
    mobileSmall: sizeSpacing04,
    // 16px
    mobile: sizeSpacing04,
    tablet: sizeSpacing05,
    // 24px
    desktop: sizeSpacing05,
    desktopLarge: sizeSpacing05
  },
  paddingWidth: {
    mobileSmall: sizeSpacing04,
    // 16px
    mobile: sizeSpacing06,
    // 32px
    tablet: sizeSpacing08,
    // 48px
    desktop: sizeSpacing08,
    desktopLarge: sizeSpacing08
  },
  container: {
    mobileSmall: 'full',
    // 'full' = max-width: 100%
    mobile: 'full',
    // 'full' = max-width: 100%
    tablet: 'full',
    // 'full' = max-width: 100%
    desktop: breakpointLg,
    // max-width: 1280px
    desktopLarge: breakpointXl // max-width: 1440px

  },
  breakpoints: {
    mobileSmall: sizeSpacing04,
    mobile: breakpointSm,
    // 768px
    tablet: breakpointMd,
    // 1024px
    desktop: breakpointLg,
    // 1280px
    desktopLarge: breakpointXl // 1440px

  },
  media: {}
};

function makeMedia(media) {
  return (...args) => css(["@media ", "{", "}"], media, css(...args));
}

const resolveConfig = () => {
  const conf = _extends({}, BASE_CONF);

  conf.media = Object.keys(conf.breakpoints).reduce((media, breakpoint) => {
    const breakpointWidth = conf.breakpoints[breakpoint];
    const mediaBP = media;
    mediaBP[breakpoint] = makeMedia(breakpointWidth && "(min-width: ".concat(breakpointWidth, ")"));
    return mediaBP;
  }, {});
  return conf;
};

export default function config(props = {}) {
  const conf = resolveConfig(props);
  return conf;
}