import React from 'react'
import styled from 'styled-components'
import { GridContainer, GridRow, GridColumn, PageHeader as StyledPageHeader } from '@resultadosdigitais/tangram-components'
import { ArrowLeft } from '@resultadosdigitais/tangram-react-icons'
import { useTranslation } from 'react-i18next'
import Breadcrumb from '../../components/Breadcrumb'

const Title = styled(StyledPageHeader)`
  padding-top: 0px;
`

const Header = ({ className, ...rest }) => {
  const { t } = useTranslation()
  const settingsUrl = '/app#/settings'

  return (
    <GridContainer>
      <GridRow>
        <GridColumn mobile={12} tablet={12} desktop={12}>
          <header className={className} {...rest}>
            <Breadcrumb href={settingsUrl}>
              <ArrowLeft /> {t('breadcrumb')}
            </Breadcrumb>
            <Title>{t('title')}</Title>
          </header>
        </GridColumn>
      </GridRow>
    </GridContainer>
  )
}

Header.displayName = 'Header'

export default Header
