import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import styled from 'styled-components'
import {
  GridRow as GridRowStyle,
  GridColumn as GridColumnStyle,
  FormLabel,
  RadioButton as FormRadio,
  Button,
  Text,
  Link as LinkStyle,
} from '@resultadosdigitais/tangram-components'
import { RadioCard } from '../../../components'
import { useLayout } from '../../../layout'
import { MergeUpdate, Duplicate } from '../images'
import { Panel, usePanelNavigation, Title, Subtitle, Description } from './Panels'

const GridRow = styled(GridRowStyle)`
  &:nth-child(3) {
    margin-top: var(--size-spacing-06);
  }
`

const GridColumn = styled(GridColumnStyle)`
  flex-wrap: wrap;
`

const Link = styled(LinkStyle)`
  cursor: pointer;
  font: inherit;
`

const RadioButton = styled(({ className, id, label, checked, helper, children, ...rest }) => (
  <div className={className}>
    <FormRadio id={id} checked={checked} hideLabel {...rest} />
    <FormLabel htmlFor={id}>
      <Text as="span" token={Text.tokens.TEXT_MD_BOLD} textColor="--color-gray-100">
        {label}
      </Text>
      <Text as="span" token={Text.tokens.TEXT_SM_REGULAR} textColor="--color-gray-80">
        {helper}
      </Text>
      {checked && children}
    </FormLabel>
  </div>
))`
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  width: 100%;

  & + & {
    margin-top: var(--size-spacing-04);
  }

  [class^='RadioButton__Root'] {
    margin: 0 var(--size-spacing-02) 0 0;
  }

  [class^='FormLabel__Root'] {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }

  [class^='Text__Root'] {
    margin: 0 0 var(--size-spacing-02) 0;
  }
`

const Select = styled.select`
  width: 250px;
`

const names = {
  DUPLICATED: 'duplicated',
  OWNER: 'owner',
  INDIVIDUAL: 'individual',
  ALL: 'all',
}

const values = {
  MERGE_UPDATE: 'merge_update',
  DUPLICATE: 'duplicate',
  INDIVIDUAL: 'individual',
  TEAM: 'team',
  ALL: 'all',
}

const WizardPreferencesPanel = ({ data, onDataChange, onDataSave }) => {
  const { toggleSidebar } = useLayout()
  const { t } = useTranslation()
  const getDefaultValues = () => ({ [names.DUPLICATED]: values.MERGE_UPDATE })
  const [selectedValue, setSelectedValue] = useState(data.preferences || getDefaultValues())
  const isChecked = (name, value) => selectedValue[name] === value

  const { openPanel } = usePanelNavigation()
  const handlePanelOpen = step => () => openPanel({ query: { step } })
  const handleChangeValue = ({ target: { name, value } }) => {
    const newSelectedValues = { ...selectedValue, [name]: value }
    setSelectedValue(newSelectedValues)
    onDataChange({ preferences: newSelectedValues })
  }

  const labels = {
    [values.MERGE_UPDATE]: t('preferences_merge_update_label'),
    [values.DUPLICATE]: t('preferences_duplicate_label'),
    [values.INDIVDUAL]: t('preferences_individual_owner_label'),
    [values.TEAM]: t('preferences_team_owner_label'),
    [values.ALL]: t('preferences_all_owner_label'),
  }

  return (
    <Panel>
      <GridRow>
        <GridColumn mobile={12} tablet={12} desktop={12}>
          <Title>{t('preferences_panel_title')}</Title>
          <Subtitle marginBottom="size-spacing-02">{t('preferences_panel_duplicated_subtitle')}</Subtitle>
          <Description marginBottom="size-spacing-05">
            {t('preferences_panel_duplicated_description')} <Link onClick={toggleSidebar}>{t('preferences_panel_duplicated_link')}</Link>.
          </Description>
        </GridColumn>
      </GridRow>
      <GridRow>
        <GridColumn mobile={6} tablet={6} desktop={6}>
          <RadioCard
            id={values.MERGE_UPDATE}
            name={names.DUPLICATED}
            value={values.MERGE_UPDATE}
            label={labels[values.MERGE_UPDATE]}
            helper={t('preferences_merge_update_helper')}
            src={MergeUpdate}
            alt={labels[values.MERGE_UPDATE]}
            checked={isChecked(names.DUPLICATED, values.MERGE_UPDATE)}
            onChange={handleChangeValue}
          />
        </GridColumn>
        <GridColumn mobile={6} tablet={6} desktop={6}>
          <RadioCard
            id={values.DUPLICATE}
            name={names.DUPLICATED}
            value={values.DUPLICATE}
            label={labels[values.DUPLICATE]}
            helper={t('preferences_duplicate_helper')}
            src={Duplicate}
            alt={labels[values.DUPLICATE]}
            checked={isChecked(names.DUPLICATED, values.DUPLICATE)}
            onChange={handleChangeValue}
          />
        </GridColumn>
      </GridRow>
      <GridRow>
        <GridColumn mobile={12} tablet={12} desktop={12}>
          <Subtitle marginBottom="size-spacing-05">{t('preferences_panel_owners_subtitle')}</Subtitle>
          <RadioButton
            label={labels[values.INDIVDUAL]}
            id={values.INDIVIDUAL}
            name={names.OWNER}
            value={values.INDIVIDUAL}
            helper={t('preferences_individual_owner_helper')}
            checked={isChecked(names.OWNER, values.INDIVIDUAL)}
            onChange={handleChangeValue}
          >
            <Select name={names.Individual} />
          </RadioButton>
          <RadioButton
            label={labels[values.TEAM]}
            id={values.TEAM}
            name={names.OWNER}
            value={values.TEAM}
            helper={t('preferences_team_owner_helper')}
            checked={isChecked(names.OWNER, values.TEAM)}
            onChange={handleChangeValue}
          />
          <RadioButton
            label={labels[values.ALL]}
            id={values.ALL}
            name={names.OWNER}
            value={values.ALL}
            helper={t('preferences_all_owner_helper')}
            checked={isChecked(names.OWNER, values.ALL)}
            onChange={handleChangeValue}
          >
            <Select name={names.ALL} />
          </RadioButton>
        </GridColumn>
      </GridRow>
      <Panel.Footer>
        <Button kind={Button.kinds.secondary} onClick={onDataSave}>
          {t('save_exit')}
        </Button>
        <Button kind={Button.kinds.secondary} onClick={handlePanelOpen('3')}>
          {t('back')}
        </Button>
        <Button kind={Button.kinds.primary} onClick={() => onDataSave({ step: '5' })}>
          {t('next')}
        </Button>
      </Panel.Footer>
    </Panel>
  )
}

export { WizardPreferencesPanel as default, names, values }
