function formatExtensionsText(text) {
  const extensions = text.replace(' ', '').split(',');
  return extensions.length === 1 ? extensions[0] : replaceLastComma(extensions.join(', '), ' ou');
}

function replaceLastComma(text, replacement) {
  const lastCommaIndex = text.lastIndexOf(',');
  return replaceCharAtIndex(lastCommaIndex, replacement, text);
}

function replaceCharAtIndex(index, replacement, text) {
  return "".concat(text.substring(0, index)).concat(replacement).concat(text.substring(index + 1));
}

export { formatExtensionsText };