import React from 'react'
import { useLocation, Switch, Route } from 'react-router-dom'
import { EmptyState, GeneralData, ProductService, OtherTool } from '../pages'

const useQuery = () => new URLSearchParams(useLocation().search)

const pathnames = {
  EMPTY_STATE: '/',
  GENERAL_DATA: '/dados-gerais',
  PRODUCTS_SERVICES: '/produtos-servicos',
  OTHERS_TOOLS: '/outras-ferramentas',
}

const Routes = () => (
  <Switch>
    <Route path={pathnames.EMPTY_STATE} exact>
      <EmptyState />
    </Route>
    <Route path={pathnames.GENERAL_DATA} exact>
      <GeneralData />
    </Route>
    <Route path={`${pathnames.GENERAL_DATA}/:id`}>
      <GeneralData />
    </Route>
    <Route path={pathnames.PRODUCTS_SERVICES}>
      <ProductService />
    </Route>
    <Route path={pathnames.OTHERS_TOOLS}>
      <OtherTool />
    </Route>
  </Switch>
)

export { Routes as default, pathnames, useQuery }
