import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { useTranslation } from 'react-i18next'
import { GridRow, GridColumn, Button, Table } from '@resultadosdigitais/tangram-components'
import { ArrowRight, CheckmarkCircle, ExclamationTriangle } from '@resultadosdigitais/tangram-react-icons'
import { colorGreen40, colorYellow60 } from '@resultadosdigitais/tangram-design-tokens'
import fieldsResource from '../../../resources/fields'
import matrixService from '../../../services/matrix'
import SpreadsheetColumnExcerpt from '../components/SpreadsheetColumnExcerpt'
import { Panel, usePanelNavigation, Title } from './Panels'

const TableContainer = styled.div`
  margin-top: var(--size-spacing-06);
`

const SelectContainer = styled.div`
  display: flex;
  select {
    margin-left: var(--size-spacing-03);
    width: 335px;
  }
`

const Checkbox = styled.input`
  margin-right: 5px;
`

const WizardFieldsCombinationPanel = ({ data, onDataChange, onDataSave }) => {
  const { t } = useTranslation()
  const { openPanel } = usePanelNavigation()
  const [fieldGroups, setFieldGroups] = useState([])
  const handlePanelOpen = step => () => openPanel({ query: { step } })
  const handleSpreadsheetHeader = ({ target: { checked } }) => {
    const newData = { ...data, has_header: checked }
    onDataChange(newData, { shouldPersist: true })
  }
  const handleFieldSelect = ({ target: { value } }, columnIndex) => {
    const newData = { ...data, col_map: { ...data.col_map, [columnIndex]: value } }
    handleColMapChange(newData, value, columnIndex)
  }
  const handleColumnRemoval = ({ target: { checked } }, columnIndex) => {
    const value = checked ? 'ignored' : ''
    const newData = { ...data, col_map: { ...data.col_map, [columnIndex]: value } }
    handleColMapChange(newData, value, columnIndex)
  }
  const handleColMapChange = (newData, colMapValue, columnIndex) => {
    if (!colMapValue) delete newData.col_map[columnIndex]
    onDataChange(newData, { shouldPersist: true })
  }

  useEffect(() => {
    if (!fieldGroups.length) fieldsResource.get().then(response => setFieldGroups(response.data.fields))
  }, [fieldGroups])

  return (
    <Panel>
      <GridRow>
        <GridColumn mobile={12} tablet={12} desktop={12}>
          <Title>{t('fields_combination_panel_title')}</Title>
        </GridColumn>
      </GridRow>
      <GridRow>
        <GridColumn mobile={12} tablet={12} desktop={12}>
          <label>
            <Checkbox type="checkbox" checked={data.has_header} onChange={handleSpreadsheetHeader} />
            {t('fields_combination_spreadsheet_header')}
          </label>
        </GridColumn>
      </GridRow>
      <GridRow>
        <GridColumn mobile={12} tablet={12} desktop={12}>
          <TableContainer>
            <Table summary="Tabela de dados do RD Station Marketing">
              <Table.Head>
                <Table.Row>
                  <Table.Cell as="th">{t('fields_combination_status')}</Table.Cell>
                  <Table.Cell as="th">{t('fields_combination_spreadsheet_column')}</Table.Cell>
                  <Table.Cell as="th">{t('fields_combination_crm_field')}</Table.Cell>
                  <Table.Cell as="th">{t('fields_combination_action')}</Table.Cell>
                </Table.Row>
              </Table.Head>
              <Table.Body>
                {buildColumnsFromRows(data.rows).map((columnItems, index) => (
                  <Table.Row key={index}>
                    <Table.Cell>{buildStatusIcon(data.col_map, index)}</Table.Cell>
                    <Table.Cell>
                      <SpreadsheetColumnExcerpt
                        hasHeader={data.has_header}
                        columnItems={columnItems}
                        isIgnored={shouldIgnoreColumn(data.col_map, index)}
                      />
                    </Table.Cell>
                    <Table.Cell>
                      <SelectContainer>
                        <ArrowRight />
                        <select
                          name={`field-select-${index}`}
                          value={getFieldSelectValue(data, index)}
                          disabled={shouldIgnoreColumn(data.col_map, index)}
                          onChange={evt => handleFieldSelect(evt, index)}
                        >
                          <option value="">{t('fields_combination_combine_or_create_field')}</option>
                          {buildFieldSelectOptions(fieldGroups)}
                        </select>
                      </SelectContainer>
                    </Table.Cell>
                    <Table.Cell>
                      <label>
                        <Checkbox
                          type="checkbox"
                          checked={shouldIgnoreColumn(data.col_map, index)}
                          onChange={evt => handleColumnRemoval(evt, index)}
                        />
                        {t('fields_combination_do_not_import')}
                      </label>
                    </Table.Cell>
                  </Table.Row>
                ))}
              </Table.Body>
            </Table>
          </TableContainer>
        </GridColumn>
      </GridRow>
      <Panel.Footer>
        <p>
          {t('fields_combination_overall_counter', countMappedFields(data))} {t('fields_combination_required_fields_counter', { rest: '1' })}
        </p>
        <Button kind={Button.kinds.secondary} onClick={onDataSave}>
          {t('save_exit')}
        </Button>
        <Button kind={Button.kinds.secondary} onClick={handlePanelOpen('2')}>
          {t('back')}
        </Button>
        <Button kind={Button.kinds.primary} onClick={() => onDataSave({ step: '4' })} disabled={shouldDisableNextStepButto(data)}>
          {t('next')}
        </Button>
      </Panel.Footer>
    </Panel>
  )
}

function buildColumnsFromRows(rows) {
  return matrixService.buildColumnsFromRows(rows)
}

function buildStatusIcon(colMap, index) {
  if (!colMap || !colMap[index]) return <ExclamationTriangle fill={colorYellow60} />
  if (colMap[index] !== 'ignored') return <CheckmarkCircle fill={colorGreen40} />
}

function getFieldSelectValue({ col_map }, index) {
  return (col_map && col_map[index]) || ''
}

function shouldIgnoreColumn(colMap, columnIndex) {
  return colMap && colMap[columnIndex] === 'ignored'
}

function buildFieldSelectOptions(fieldGroups) {
  return fieldGroups.map(group => {
    return (
      <optgroup label={group.label}>
        {group.options.map(option => (
          <option value={option.key}>{option.label}</option>
        ))}
      </optgroup>
    )
  })
}

function countMappedFields(data) {
  const end = data.columns || 0
  const start = countOverallMappedFields(data.col_map)
  return { start, end }
}

function countOverallMappedFields(colMap) {
  return colMap ? Object.keys(colMap).length : 0
}

function shouldDisableNextStepButto(data) {
  return data.columns !== countOverallMappedFields(data.col_map)
}

export default WizardFieldsCombinationPanel
