import ENV from '@environment'
import { baseResource } from '@resultadosdigitais/crm-core'

const BASE_URL = ENV.API.BASE_URL

const _public = {}

_public.save = ({ file, type }) => {
  const data = buildImportData({ file, type })
  return baseResource.post(`${BASE_URL}/imports/file`, data, {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
  })
}

_public.update = (id, data) => baseResource.put(`${BASE_URL}/imports/${id}`, data)

_public.get = id => baseResource.get(`${BASE_URL}/imports/${id}`)

function buildImportData({ file, type }) {
  const data = new FormData()
  data.append('file', file)
  data.append('import_type', type)
  return data
}

export default _public
