import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import styled from 'styled-components'
import { GridRow as GridRowStyle, GridColumn as GridColumnStyle, Button as ButtonStyle } from '@resultadosdigitais/tangram-components'
import { Checkmark, Import } from '@resultadosdigitais/tangram-react-icons'
import { RadioCard, UnorderedList, Video } from '../../../components'
import { OrganizationsContacts, Deals } from '../images'
import { Panel, usePanelNavigation, Title, Subtitle } from './Panels'
import { ORGANIZATIONS_CONTACTS, DEALS } from '../constants/importTypes'

const GridRow = styled(GridRowStyle)`
  &:nth-child(3) {
    margin-top: var(--size-spacing-06);
  }
`

const GridColumn = styled(GridColumnStyle)`
  flex-wrap: wrap;
`

const Icon = styled(Checkmark)`
  width: var(--text-size-xs);
  height: var(--text-size-xs);
  min-height: var(--text-size-xs);
  min-width: var(--text-size-xs);
  margin-right: calc(var(--size-spacing-02) + (var(--size-spacing-01) / 2));
`

const List = styled(UnorderedList)`
  margin-bottom: var(--size-spacing-05);
`

List.Item = UnorderedList.Item

const Button = styled(ButtonStyle)`
  & + & {
    margin-left: var(--size-spacing-05);
  }
`

const WizardSheetContentPanel = ({ data, onDataChange }) => {
  const { t } = useTranslation()
  const { query, openPanel } = usePanelNavigation()
  const [type, setType] = useState(data.type || query.get('type'))
  const isChecked = value => type === value
  const handleOpenPanel = step => () => openPanel({ query: { step, type } })
  const handleTypeChange = ({ target: { value } }) => {
    setType(value)
    onDataChange({ type: value })
  }

  const labels = {
    [ORGANIZATIONS_CONTACTS]: t('sheet_content_organizations_contacts_label'),
    [DEALS]: t('sheet_content_deals_label'),
  }

  return (
    <Panel>
      <GridRow>
        <GridColumn mobile={12} tablet={12} desktop={12}>
          <Title>{t('sheet_content_panel_title')}</Title>
        </GridColumn>
      </GridRow>
      <GridRow>
        <GridColumn mobile={6} tablet={6} desktop={6}>
          <RadioCard
            id={ORGANIZATIONS_CONTACTS}
            name={ORGANIZATIONS_CONTACTS}
            value={ORGANIZATIONS_CONTACTS}
            label={labels[ORGANIZATIONS_CONTACTS]}
            helper={t('sheet_content_organizations_contacts_helper')}
            src={OrganizationsContacts}
            alt={labels[ORGANIZATIONS_CONTACTS]}
            checked={isChecked(ORGANIZATIONS_CONTACTS)}
            onChange={handleTypeChange}
          />
        </GridColumn>
        <GridColumn mobile={6} tablet={6} desktop={6}>
          <RadioCard
            id={DEALS}
            name={DEALS}
            value={DEALS}
            label={labels[DEALS]}
            helper={t('sheet_content_deals_helper')}
            src={Deals}
            alt={labels[DEALS]}
            checked={isChecked(DEALS)}
            onChange={handleTypeChange}
          />
        </GridColumn>
      </GridRow>
      {type && (
        <GridRow>
          <GridColumn mobile={6} tablet={6} desktop={6}>
            <Subtitle marginBottom="size-spacing-04">{`${t('sheet_content_list_subtitle_tips')} ${labels[type]}`}</Subtitle>
            <List icon={Icon}>
              <List.Item>{t('sheet_content_list_item_company_name')}</List.Item>
              <List.Item>{t('sheet_content_list_item_reset_data')}</List.Item>
              <List.Item>{t('sheet_content_list_item_date_format')}</List.Item>
            </List>
            <Subtitle marginBottom="size-spacing-04">{t('sheet_content_list_subtitle_example')}</Subtitle>
            <Button kind={Button.kinds.secondary} startIcon={<Import />}>
              {t('sheet_content_list_example_csv')}
            </Button>
            <Button kind={Button.kinds.secondary} startIcon={<Import />}>
              {t('sheet_content_list_example_csv')}
            </Button>
          </GridColumn>
          <GridColumn mobile={6} tablet={6} desktop={6} align="flex-end" justify="flex-end">
            <Video width="337" height="187" src="https://www.youtube.com/embed/Nq8xDwGr2mQ" />
          </GridColumn>
        </GridRow>
      )}
      <Panel.Footer>
        <Button kind={Button.kinds.primary} onClick={handleOpenPanel('1')} disabled={!type}>
          {t('next')}
        </Button>
      </Panel.Footer>
    </Panel>
  )
}

export { WizardSheetContentPanel as default }
