import React from 'react'
import styled from 'styled-components'
import { useTranslation } from 'react-i18next'
import { withContext } from '@resultadosdigitais/front-hub/react'
import { GridRow, GridColumn, Button, Link, Text } from '@resultadosdigitais/tangram-components'
import { Panel, usePanelNavigation, Title } from './Panels'
import { DEALS } from '../constants/importTypes'
import { DATA_PROCESSING, COMMUNICATIONS } from '../constants/legalBaseCategories'

const PanelContainer = styled.div`
  max-width: 840px;
`

const PanelSelect = styled.select`
  margin-bottom: var(--size-spacing-05);
  width: 100%;
  max-width: 375px;
`

const WizardPrivacySettings = ({ data, onDataChange, onDataSave, legalBases }) => {
  const { t } = useTranslation()
  const { query, openPanel } = usePanelNavigation()
  const typeText = query.get('type') === DEALS ? t('deals') : t('companies_customers')
  const handlePanelOpen = step => () => openPanel({ query: { step } })
  const handleDataChange = ({ target: { name, value } }) => {
    const newLegalBases = getSelectedLegalBases().filter(({ category }) => category !== name)
    newLegalBases.push({ category: name, type: value })
    onDataChange({ legal_bases: newLegalBases })
  }
  const isMissingLegalBaseDefinition = () => {
    const categories = [DATA_PROCESSING, COMMUNICATIONS]
    const filledLegalBases = categories.filter(category => isLegalBaseCategoryFilled(category))
    return filledLegalBases.length !== categories.length
  }
  const isLegalBaseCategoryFilled = category => {
    const legalBase = findSelectedLegalBaseByCategory(category)
    return !!(legalBase && legalBase.type)
  }
  const getSelectedLegalBaseTypeByCategory = category => {
    const legalBase = findSelectedLegalBaseByCategory(category)
    return legalBase ? legalBase.type : ''
  }
  const findSelectedLegalBaseByCategory = category => {
    return getSelectedLegalBases().find(legalBase => {
      return legalBase.category === category
    })
  }
  const getSelectedLegalBases = () => {
    const { legal_bases } = data
    return legal_bases || []
  }

  return (
    <Panel>
      <PanelContainer>
        <GridRow>
          <GridColumn mobile={12} tablet={12} desktop={12}>
            <Title>{t('privacy_settings_panel_title')}</Title>
          </GridColumn>
        </GridRow>
        <GridRow>
          <GridColumn mobile={12} tablet={12} desktop={12}>
            <Text token={Text.tokens.TEXT_MD_REGULAR}>
              {t('privacy_settings_panel_description', { type: typeText })} <Link href="">{t('learn_more')}</Link>
            </Text>
          </GridColumn>
        </GridRow>
        <GridRow>
          <GridColumn mobile={12} tablet={12} desktop={12}>
            <Text token={Text.tokens.TEXT_MD_BOLD}>{t('privacy_settings_processing_data_question', { type: typeText })}</Text>
          </GridColumn>
        </GridRow>
        <GridRow>
          <GridColumn mobile={12} tablet={12} desktop={12}>
            <PanelSelect value={getSelectedLegalBaseTypeByCategory(DATA_PROCESSING)} name={DATA_PROCESSING} onChange={handleDataChange}>
              <option value="">Selecione</option>
              {buildLegalBaseOptions(legalBases)}
            </PanelSelect>
          </GridColumn>
        </GridRow>
        <GridRow>
          <GridColumn mobile={12} tablet={12} desktop={12}>
            <Text token={Text.tokens.TEXT_MD_BOLD}>{t('privacy_settings_processing_communication_question', { type: typeText })}</Text>
          </GridColumn>
        </GridRow>
        <GridRow>
          <GridColumn mobile={12} tablet={12} desktop={12}>
            <PanelSelect value={getSelectedLegalBaseTypeByCategory(COMMUNICATIONS)} name={COMMUNICATIONS} onChange={handleDataChange}>
              <option value="">Selecione</option>
              {buildLegalBaseOptions(legalBases)}
            </PanelSelect>
          </GridColumn>
        </GridRow>
      </PanelContainer>
      <Panel.Footer>
        <Button kind={Button.kinds.secondary} onClick={onDataSave}>
          {t('save_exit')}
        </Button>
        <Button kind={Button.kinds.secondary} onClick={handlePanelOpen('1')}>
          {t('back')}
        </Button>
        <Button kind={Button.kinds.primary} onClick={() => onDataSave({ step: '3' })} disabled={isMissingLegalBaseDefinition()}>
          {t('next')}
        </Button>
      </Panel.Footer>
    </Panel>
  )
}

function buildLegalBaseOptions({ types }) {
  return types.map(({ slug, title }, index) => (
    <option value={slug} key={index}>
      {title}
    </option>
  ))
}

export default withContext(WizardPrivacySettings)
