import { useState, useEffect } from 'react';
export const useVisibility = (open, delay = 500) => {
  const [visible, setVisible] = useState(open);
  useEffect(() => {
    const tid = setTimeout(() => {
      setVisible(open);
    }, open ? 0 : delay);
    return () => {
      clearTimeout(tid);
    };
  }, [open, delay]);
  return visible;
};