import fieldsService from '../fields'
import matrixService from '../matrix'

const _public = {}

_public.buildDataGoups = (rows, colMap, fields) => {
  const columns = matrixService.buildColumnsFromRows(rows)
  const groups = []
  Object.keys(colMap).forEach(colIndex => {
    const fieldKey = colMap[colIndex]
    const groupName = fieldsService.getGroupNameByFieldKey(fields, fieldKey)
    const group = groups.find(group => group.name === groupName)
    if (!group) groups.push(buildGroup(groupName, columns, colIndex, fieldKey, fields))
    else group.columns.push(buildGroupColumn(columns, colIndex, fieldKey, fields))
  })
  return groups
}

function buildGroup(groupName, columns, colIndex, fieldKey, fields) {
  return {
    name: groupName,
    iconName: getGroupIconName(groupName),
    columns: [buildGroupColumn(columns, colIndex, fieldKey, fields)],
  }
}

function buildGroupColumn(allColumns, colIndex, fieldKey, fields) {
  return {
    items: allColumns[colIndex],
    fieldName: fieldsService.getFieldNameByFieldKey(fields, fieldKey),
  }
}

function getGroupIconName(groupName) {
  const availableIconNames = {
    'Empresa/Cliente': 'Building',
    Contato: 'Person',
    Oportunidade: 'Case',
  }
  return availableIconNames[groupName]
}

export default _public
