import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'
import { Card, Text, FormLabel, RadioButton } from '@resultadosdigitais/tangram-components'

const Container = styled(Card)`
  display: flex;
  align-items: flex-start;
  width: 100%;

  [class^='RadioButton__Root'] {
    margin: 0 var(--size-spacing-02) 0 0;
  }

  [class^='RadioButton__Input'] {
    min-width: 24px;
    min-height: 24px;
  }
`

const RadioLabel = styled(FormLabel)`
  display: flex;
  flex-direction: column;
  margin-right: var(--size-spacing-04);

  [class^='Text__Root']:nth-child(1) {
    margin-bottom: var(--size-spacing-01);
  }
`

const Image = styled.img`
  width: 120px;
`

const RadioCard = ({ id, name, value, label: text, helper, src, alt, checked, onClick, onChange, ...rest }) => {
  const handleChange = () => {
    const e = { target: { name, value, checked: !checked } }
    typeof onChange === 'function' && onChange(e)
  }

  const handleClick = e => {
    typeof onClick === 'function' && onClick(e)
    handleChange()
  }

  return (
    <Container active={checked} clickable onClick={handleClick}>
      <RadioButton {...rest} role="radio" id={id} name={name} value={value} checked={checked} onChange={handleChange} hideLabel />
      <RadioLabel htmlFor={id}>
        <Text as="span" token={Text.tokens.TEXT_MD_BOLD} textColor="--color-gray-100">
          {text}
        </Text>
        <Text as="span" token={Text.tokens.TEXT_SM_REGULAR} textColor="--color-gray-90">
          {helper}
        </Text>
      </RadioLabel>
      <Image src={src} alt={alt} />
    </Container>
  )
}

RadioCard.propTypes = {
  value: PropTypes.string,
  label: PropTypes.string.isRequired,
  helper: PropTypes.string.isRequired,
  src: PropTypes.string.isRequired,
  alt: PropTypes.string,
  checked: PropTypes.bool,
  onChange: PropTypes.func,
}

export default RadioCard
