import React from 'react'
import { useHistory } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import styled from 'styled-components'
import { GridContainer, GridRow, GridColumn, Snackbar, Card as Container, Text, Button, Link } from '@resultadosdigitais/tangram-components'
import { GeneralData, ProductService, OtherTool } from './images'
import { useLayout } from '../../layout'
import { pathnames } from '../../routes'

const Card = styled(Container)`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
`

const Title = styled(Text)`
  margin-bottom: calc(var(--size-spacing-05) - var(--size-spacing-01));
`

const Description = styled(Text)`
  margin-bottom: 0;
`

const Image = styled.img`
  height: 120px;
  margin-bottom: calc(var(--size-spacing-05) - var(--size-spacing-01));
`

const Method = ({ title, description, image, ...rest }) => (
  <Card {...rest} clickable>
    <Image src={image} alt="illustration" />
    <Title token={Text.tokens.TEXT_LG_BOLD}>{title}</Title>
    <Description token={Text.tokens.TEXT_MD_REGULAR}>{description}</Description>
  </Card>
)

const Action = styled(({ className }) => {
  const { t } = useTranslation()
  const { toggleSidebar } = useLayout()

  return (
    <>
      <Button className={className} kind={Button.kinds.secondary} onClick={toggleSidebar}>
        {t('action_configure')}
      </Button>
      <Link
        className={className}
        href="https://crmsupport.rdstation.com.br/hc/pt-br/articles/360018692431-Como-importar-minhas-planilhas-"
        target="_blank"
      >
        {t('action_know_more')}
      </Link>
    </>
  )
})`
  & + & {
    margin-left: var(--size-spacing-05);
  }
`

const Helper = styled(Snackbar).attrs(() => ({
  show: true,
  kind: Snackbar.kinds.help,
}))`
  margin-bottom: var(--size-spacing-06);
`

const EmptyState = () => {
  let history = useHistory()
  const { t } = useTranslation()
  const openPage = path => () => history.push(path)
  return (
    <GridContainer>
      <GridRow>
        <GridColumn mobile={12} tablet={12} dektop={12}>
          <Helper title={t('helper_configuration_title')} message={t('helper_configuration_description')} action={<Action />} />
        </GridColumn>
      </GridRow>
      <GridRow>
        <GridColumn mobile={12} tablet={4} desktop={4}>
          <Method
            title={t('method_general_data_title')}
            description={t('method_general_data_description')}
            image={GeneralData}
            onClick={openPage(pathnames.GENERAL_DATA)}
          />
        </GridColumn>
        <GridColumn mobile={12} tablet={4} desktop={4}>
          <Method
            title={t('method_product_service_title')}
            description={t('method_product_service_description')}
            image={ProductService}
            onClick={openPage(pathnames.PRODUCTS_SERVICES)}
          />
        </GridColumn>
        <GridColumn mobile={12} tablet={4} desktop={4}>
          <Method
            title={t('method_other_tool_title')}
            description={t('method_other_tool_description')}
            image={OtherTool}
            onClick={openPage(pathnames.OTHERS_TOOLS)}
          />
        </GridColumn>
      </GridRow>
    </GridContainer>
  )
}

export default EmptyState
