import React, { Children } from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'
import { Text } from '@resultadosdigitais/tangram-components'

const Item = styled(Text).attrs(() => ({
  forwardedAs: 'li',
  token: Text.tokens.TEXT_SM_REGULAR,
}))`
  display: flex;
  align-items: center;
`

const UnorderedList = styled(({ icon: Icon, children, ...rest }) => {
  const items = Children.map(children, ({ props: { children, ...rest } }) => (
    <Item {...rest}>
      <Icon /> {children}
    </Item>
  ))

  return <ul {...rest}>{items}</ul>
})`
  margin: 0;
  padding: 0;
  list-style: none;
`
UnorderedList.displayName = 'UnorderedList'

UnorderedList.propTypes = {
  icon: PropTypes.elementType.isRequired,
}

UnorderedList.Item = Item

UnorderedList.Item.displayName = 'UnorderedList.Item'

export default UnorderedList
