/** @format */

import { createBrowserHistory } from 'history';

const browserHistory = createBrowserHistory();
const dispatchers = [];

// TODO: do a better job here
function state(append) {
  const parsedUrl = new URL(window.location.href);
  const current = JSON.parse(atob(parsedUrl.searchParams.get('f') || 'e30='));
  if (append) {
    current[append.name] = append;
    parsedUrl.searchParams.set('f', btoa(JSON.stringify(current)));
    browserHistory.push({ search: parsedUrl.searchParams.toString() });
  }
  return current;
}

function broadcast() {
  const previous = state();
  dispatchers.map(dispatch => dispatch(previous));
}

const manager = {
  event(params) {
    if (this.unListen) this.unListen();
    state(params);
    this.unListen = browserHistory.listen(broadcast);
  },
  listen(history, name) {
    const cache = {};
    const unListen = history.listen(params => {
      if (cache[params.key] || params.rehydratation) return;
      cache[params.key] = true;
      this.event({
        ...params,
        name
      });
    });
    const unRegister = this.register(history, name);

    return () => {
      unListen();
      unRegister();
    };
  },
  register(history, name) {
    const dispacher = ((closureHistory, closureName) => currentState => {
      if (currentState[closureName]) {
        closureHistory.push({
          rehydratation: true,
          ...currentState[closureName]
        });
      }
    })(history, name);

    dispacher(state());
    dispatchers.push(dispacher);

    return () => {
      const index = dispatchers.indexOf(dispacher);
      if (index > -1) {
        dispatchers.splice(index, 1);
      }
    };
  }
};

function listen({ name }) {
  return history => manager.listen(history, name);
}

export default function(microfrontend) {
  return {
    listen: listen(microfrontend)
  };
}
